<template>
  <div class="insertWord">
    <el-form ref="form" :model="form" label-width="80px" :label-position="labelPosition">
      <el-form-item label="單字">
        <el-input v-model="form.content"></el-input>
      </el-form-item>
      <el-form-item label="中文意思">
        <el-input v-model="form.interpretation"></el-input>
      </el-form-item>
      <el-form-item label="詞性">
        <el-select v-model="form.partOfSpeech" placeholder="請選擇詞性">
          <el-option label="名詞(noun)" value="noun"></el-option>
          <el-option label="動詞(verb)" value="verb"></el-option>
          <el-option label="形容詞(adjective)" value="adjective"></el-option>
          <el-option label="副詞(adverb)" value="adverb"></el-option>
          <el-option label="縮寫(abbreviation)" value="abbreviation"></el-option>
          <el-option label="冠詞(article)" value="article"></el-option>
          <el-option label="連接詞(conjunction)" value="conjunction"></el-option>
          <el-option label="介繫詞(preposition)" value="preposition"></el-option>
          <el-option label="代名詞(pronoun)" value="pronoun"></el-option>
          <el-option label="助動詞(auxiliary-verb)" value="auxiliary-verb"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="年級">
        <el-select v-model="form.grade" placeholder="請選擇年級">
          <el-option label="國小三年級" value="3"></el-option>
          <el-option label="國小四年級" value="4"></el-option>
          <el-option label="國小五年級" value="5"></el-option>
          <el-option label="國小六年級" value="6"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="來源">
        <el-input v-model="form.source"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button @click="trigger()">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'insertWord',
  data() {
    return {
      labelPosition: 'right',
      form: {
        content: '',
        interpretation: '',
        partOfSpeech: '',
        grade: '',
        source: '',
      }
    }
  },
  methods: {
    trigger: function() {
      var self = this;


      self.allert(function(data) {

        self.notification(data)


      });

    },
    allert: function(callback) {
      callback("123");
    },
    notification: function(data) {
      console.log(data);
    },
    onSubmit() {
      // var params = new URLSearchParams();
      // params.append('word', this.form.content);
      // params.append('interpretation', this.form.interpretation);
      // params.append('partofspeech', this.form.partOfSpeech);
      // params.append('grade', this.form.grade);
      // params.append('source', this.form.source);
      // params.append('userid', "admin");
      var params={
      word: this.form.content,
      interpretation: this.form.interpretation,
      partofspeech: this.form.partOfSpeech,
      grade: this.form.grade,
      source: this.form.source,
      userid: "admin"
      }
      console.log(params);
      axios.post('//127.0.0.1:8080/elearning/word/insert', params)
        .then(response => {
          console.log(response);
        })
        .catch(error => {
          console.log(error);
          alert('网络错误，不能访问');
        })
      console.log('submit!');
    }
  }

}

</script>
<style scoped>
.insertWord {
  padding: 24px 32px 24px 32px;
  background-color: #ffffff;
  border-radius: 2px;
  box-shadow: 2px 3px 4px rgba(0, 0, 0, .2);
}

.el-select {
  display: block;
}

</style>



