<template>
	<header class="jHeader">
		<nav>
			{{msg}}
		</nav>
	</header>
</template>
<script>
	export default{
		name: 'jHeader',
		data() {
			return {
				msg:'hello'
			}
		}
	}
</script>
<style>
header{
	height: 80px;
	width:100%;
	background-color: #475669;
	position: absolute;
	z-index: 20;
}
</style>


